import React from "react"
import PropTypes from "prop-types"
import Layout from "partials/layouts/layout"
import Header from "partials/common/header"
import Footer from "partials/common/footer"

export const layoutPageStyleClassName = "str-page pb-3"

const LayoutPage = ({ children, meta, unstyled }) => (
  <Layout meta={meta}>
    <Header />
    <div className={!unstyled && layoutPageStyleClassName}>
      {children}
    </div>
    <Footer />
  </Layout>
)


LayoutPage.propTypes = {
  children: PropTypes.node,
  meta: PropTypes.object,
  unstyled: PropTypes.bool
}

LayoutPage.defaultProps = {
  children: null,
  meta: {},
  unstyled: false
}

export default LayoutPage
