import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql } from "gatsby"
import SEO from "components/seo"
import "assets/styles/index.scss"

/**
 * SEO Global params
 * @param {objecg} data from graphQL query
 * @param {object} prop from view
 */
const seoParams = (data, meta) => ({
  title: meta?.title,
  siteTitle: data?.site?.siteMetadata?.title,
  description: meta?.description || data?.site?.siteMetadata?.description,
  lang: meta?.lang,
  author: meta?.author || data?.site?.siteMetadata?.author,
  tags: meta?.tags,
  category: meta?.category,
  type: meta?.type,
})

/**
 * Data query
 */
const query = graphql`
  query SiteMetaQuery {
    site {
      siteMetadata {
        title
        description
        siteUrl
        author
        logo
      }
    }
  }
`



/**
 * View
 * @param {object} props
 * @returns {class} StaticQuery from Gatsby
 */
const Layout = ({ children, meta }) => (
  <StaticQuery
    query={query}
    render={(data) => (
      <>
        <SEO {...seoParams(data, meta)} />
        {children}
      </>
    )}
  />
)

Layout.propTypes = {
  children: PropTypes.node,
  meta: PropTypes.object
}

Layout.defaultProps = {
  children: null,
  meta: {}
}

export default Layout
